@import "loading.scss";
.container {
  max-width: 1040px;
  margin: 0 auto;
  padding: 5px;
}
.card {
  display: flex;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 50px;
}
.card__item {
  img {
    width: 100%;
  }
  width: 33%;
}
.scan {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  &__button {
    padding: 10px 60px;
    border: solid 1px #4fc3f7;
    border-radius: 20px;
    cursor: pointer;
    font-weight: 500;
    font-size: 20px;
    transition-duration : 0.5s;
  }
  &__button:hover {
    background-color: #4fc3f7;
    color: white;
  }
}
.divider {
  width: 100%;
  border-top: solid 1px;
  margin: 20px 0;
  color: #999;
}
.result {
  display: flex;
  justify-content: space-between;
  .face {
    width: 30%;
    &__image {
      display: flex;
      img {
        width: 45%;
      }
      gap: 20px;
    }
    .similarity {
      margin-top: 10px;
      text-align: center;
    }
  }
  .stactics {
    display: flex;
    width: 60%;
    justify-content: space-between;
    img {
      width: 45%;
    }
  }
}
.result-text {
  padding: 10px;
  border: solid 1px #eee;
  border-radius: 8px;
  margin-top: 40px;
  p {
    margin: 5px 0;
  }
  &__des {
    max-height: 300px;
  }
  overflow-y: scroll;
  margin-bottom: 20px;
}
.upload {
  // position: relative;
  display: flex;
  flex-direction: column;
  border: dotted 2px;
  padding: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 500px;
  border-radius: 4px;
  .input__upload {
    // position: absolute;
    // visibility: hidden;
    // height: 100%;
  }
  &__text {
    font-weight: 500;
    cursor: pointer;
    margin: 5px 0;
  }
}
.end-result {
  display: flex;
  justify-content: center;
  align-items: center;
}
.image__render {
  position: relative;
  .remove {
    position: absolute;
    right: 5px;
    top: 5px;
    cursor: pointer;
  }
}